.contentblock--quotation {

}
blockquote {
  text-align: center;
  margin: 0 auto;
  position: relative;
  @include media-breakpoint-down(sm) {
    margin-left: pxToVwMobile(19px);
    margin-right: pxToVwMobile(19px);
  }
  @include media-breakpoint-up(md) {
    max-width: 520px;
  }
  @include media-breakpoint-up(xl) {
    max-width: 820px;
  }
  .quote {
    @include fluidAll(font-size, 25px, 25px, 45px);
    @include fluidAll(line-height, 30px, 30px, 55px);
    font-family: $font-family-headlines;
    font-style: italic;
    font-weight: 300;
    @include fluidAll(padding-top, 48px, 50px, 110px);
    @include fluidAll(padding-bottom, 48px, 50px, 110px);
    @include fluidAll(padding-left, 30px, 70px, 100px);
    @include fluidAll(padding-right, 30px, 70px, 100px);
  }
}
.quotation-mark {
  @include fluid(width, 36px);
  @include fluid(height, 52px);
  @include fluid(border-radius, 10px);
  border-color: $black;
  border-style: solid;
  @include fluid(border-width, 2px);
  border-right: none;
  border-bottom: none;
  position: absolute;
}
blockquote {
  .quotation-mark {
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}
.quotation-mark--top {
  top: 0;
  left: 0;
}
.quotation-mark--bottom {
  transform: rotate(180deg);
  bottom: 0;
  right: 0;
}
