// ratios
$mobileRatio: 100%;
$tabletRatio: 100%;
$desktopRatioSmall: 100%;
$desktopRatioLarge: 52.846%;

.contentblock--teaserslider {
  @include media-breakpoint-up(teasersliderouter) {
    .container {
      max-width: map-get($grid-breakpoints, teasersliderouter);
      padding-left: 67px;
      padding-right: 67px;
    }
  }
}

.teaserslider--element {
  display: grid;
  grid-column-gap: 30px;
}
.teaserslider--element-33-66 {
  grid-template-columns: 1fr 2fr;
}
.teaserslider--element-50-50 {
  grid-template-columns: 1fr 1fr;
}
.teaserslider--element-66-33 {
  grid-template-columns: 2fr 1fr;
}
.teaserslider--item {
  img {
    width: 100%;
    height: auto;
  }
}
