@keyframes color-change-2x {
  0% {
    background: #c9bea9;
  }
  100% {
    background: #1290a5;
  }
  100% {
    background: #0d7537;
  }
}
@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 0;
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
    opacity: 1;
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}

.contentblock--parallax {
  position: relative;
  animation: color-change-2x 7s linear infinite alternate both;
  @include fluidAll(padding-top, 191px, 134px, 99px);
  @include fluid(padding-bottom, 30px);
}
.parallax--image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.parallax--image-div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: 50%;
  background-position-y: 50%;
}
.parallax--teaser {
  display: inline-block;
  @include fluidAll(padding-top, 50px, 50px, 50px);
  @include fluidAll(padding-right, 23px, 34px, 50px);
  @include fluidAll(padding-bottom, 50px, 50px, 50px);
  @include fluidAll(padding-left, 23px, 34px, 50px);
  background: #F1F1F1;
  @include fluid(border-radius, 10px);
  position: relative;
  z-index: 1;
  @include media-breakpoint-up(md) {
    max-width: 375px;
  }
  @include media-breakpoint-up(lg) {
    max-width: 575px;
  }

  h3 {
    @include fluid(margin-bottom, 30px);
  }

  .teasertext {
    @include fluidAll(font-size, 16px, 16px, 18px);
    @include fluidAll(line-height, 25px, 25px, 30px);
    @include fluid(margin-bottom, 30px);
  }

}

.frontend {
  .parallax--teaser {
    .btn {
      opacity: 0;
    }
  }
}

.contentblock--parallax {
  &.entered {
    .parallax--teaser {
      .btn {
        animation: jello-horizontal 0.9s 0.5s both;
      }
    }
  }
}

/* Fix for ios */

@supports (-webkit-touch-callout: none) {

  .parallax--image-div {
    background-attachment: initial;
  }

}

/* Everything smaller than xl */

@include media-breakpoint-down(xl) {

  .parallax--image-div {
    background-attachment: initial;
  }

}