/* Basic Visibility Classes */

@include media-breakpoint-down(sm) {
  .desktop-only {
    display: none !important;
  }
}
@include media-breakpoint-up(md) {
  .mobile-only {
    display: none !important;
  }
}

/* a11y Jump Links */

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.sr-only-focusable {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  white-space: nowrap;
  border: 0;
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    overflow: visible;
    clip: auto;
    white-space: normal;
  }
}
.a11y--jump-link {
  display: block;
  background-color: $color1;
  color: $white;
  text-decoration: none;
  margin: 0;
  padding: .5em 1em;
  position: absolute !important;
  z-index: 1000000;

  &:active, &:focus {
    display: block;
    width: 100%;
    color: $white;
    text-decoration: underline;
  }
}


// Box Shadow

.box-shadow {
  box-shadow: 0 0 pxToVwMobile(30px) $colorBorder;
  @include media-breakpoint-up(md) {
    box-shadow: 0 0 pxToVw(60px) $colorBorder;
  }
  @include media-breakpoint-up(xl) {
    box-shadow: 0 0 60px $colorBorder;
  }
}

// Text

.text-right {
  text-align: right !important;
}
.text-center {
  text-align: center !important;
}