.footer {
  background: $black;
  color: $white;
  position: relative;
  font-weight: 300;

  a {
    color: $white;
  }

  ul {
    @include reset-list;
  }
}

.footer--logo {
  background-color: $white;
  background-size: 70%;
}

.footer--social {
  li {
    display: inline-block;
  }
}

.footer--info {

}

.footer--menu {

}

/* mobile */

@include media-breakpoint-down(sm) {

  .footer {
    font-size: pxToVwMobile(16px);
    line-height: pxToVwMobile(25px);
    padding-top: pxToVwMobile(54px);
    padding-bottom: pxToVwMobile(45px);
  }

  .footer--logo {
    width: pxToVwMobile(190px);
    height: pxToVwMobile(56px);
    border-top-right-radius: pxToVwMobile(15px);
    border-bottom-right-radius: pxToVwMobile(15px);
  }

  .footer--logo {
    margin-bottom: pxToVwMobile(34px);
  }

  .footer--social {
    position: absolute;
    right: pxToVwMobile(33px);
    top: pxToVwMobile(54px);

    li {
      display: inline-block;
      margin-left: pxToVwMobile(23.25px);
    }

    a {
      display: block;
      width: pxToVwMobile(30px);
      height: pxToVwMobile(30px);
      border-radius: pxToVwMobile(30px);
      line-height: pxToVwMobile(30px);
      text-align: center;
      background: $white;
      color: $black;
      position: relative;

      i.iconfont-instagram {
        font-size: pxToVwMobile(20px);
        line-height: pxToVwMobile(30px);
      }

      i.iconfont-pinterest {
        font-size: pxToVwMobile(25px);
        line-height: pxToVwMobile(30px);
      }
    }
  }
  .footer--info {
    margin-bottom: pxToVwMobile(30px);
    padding-left: pxToVwMobile(33px);
    padding-right: pxToVwMobile(33px);

    h2 {
      font-size: pxToVwMobile(18px);
      line-height: pxToVwMobile(25px);
      font-weight: 700;
      max-width: 80%;
      margin-bottom: pxToVwMobile(30px);
    }

    li {
      margin-bottom: pxToVwMobile(6px);
    }

    i {
      margin-right: pxToVwMobile(23px);
      font-size: pxToVwMobile(20px);
      line-height: pxToVwMobile(25px);
      float: left;
    }
  }
  .footer--menu {
    padding-left: pxToVwMobile(33px);
    padding-right: pxToVwMobile(33px);

    li {
      margin-bottom: pxToVwMobile(6px);
    }
  }
}

@include media-breakpoint-up(md) {
  .footer {
    padding-top: 50px;
    padding-bottom: 50px;
    font-size: 18px;
    line-height: 30px;
    display: grid;
    grid-template-columns: 75% 25%;
    grid-template-areas:
      'fl fs'
      'fi fm';
  }
  .footer--logo {
    width: 280px;
    height: 80px;
    margin-bottom: 30px;
    grid-area: fl;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    background-position-x: 100px;
    background-size: 52%;
  }
  .footer--social {
    grid-area: fs;
    li {
      margin-right: 23px;
    }
    a {
      display: block;
      width: 30px;
      height: 30px;
      border-radius: 30px;
      line-height: 30px;
      text-align: center;
      background: $white;
      color: $black;
      position: relative;

      i.iconfont-instagram {
        font-size: 20px;
        line-height: 30px;
      }

      i.iconfont-pinterest {
        font-size: 25px;
        line-height: 30px;
      }
    }
  }
  .footer--info {
    grid-area: fi;
    padding-left: 100px;
    h2 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 13px;
    }
    i {
      margin-right:23px;
      font-size: 18px;
      line-height: 30px;
      float: left;
    }
  }
  .footer--menu {
    grid-area: fm;
    padding-right: 100px;
    display: grid;
    align-items: end;
    li {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

}

@include media-breakpoint-up(xl) {

  .footer {
    padding-top: 90px;
    padding-bottom: 90px;
    grid-template-columns: 426px auto 20%;
    grid-template-areas:
    'fl fi fs'
    'fl fi fm';
  }

  .footer--logo {
    width: 426px;
    height: 148px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    margin: 0;
    background-size: 57%;
  }

  .footer-info {
    h2 {
      margin-top: -5px;
    }
  }

  .footer--social {
    grid-area: fs;
    li {
      margin-right: 31px;
    }
    a {
      width: 40px;
      height: 40px;
      border-radius: 40px;
      line-height: 40px;

      i.iconfont-instagram {
        font-size: 23px;
        line-height: 40px;
      }

      i.iconfont-pinterest {
        font-size: 26px;
        line-height: 40px;
      }
    }
  }

  .footer--menu {
    li {
      margin-bottom: 1px;
    }
  }

}
