.contentblock--headtext {

  .headline {
    position: relative;
    &--background {
      position: absolute;
      height: 50%;
      width: 100%;
      left: 0;
      bottom: 0;
      background-color: #F1F1F1;
      z-index: 1;
    }
    h2 {
      color: $white;
      background: $color-planung;
      display: inline-block;
      @include fluidAll(padding-top, 20px, 20px, 25px);
      @include fluidAll(padding-right, 20px, 60px, 50px);
      @include fluidAll(padding-bottom, 20px, 20px, 25px);
      @include fluidAll(padding-left, 20px, 60px, 50px);
      @include fluid(border-radius, 10px);
      position: relative;
      z-index: 2;
      @include media-breakpoint-down(sm) {
        margin-left: pxToVwMobile(-20px);
        margin-right: pxToVwMobile(-20px);
      }
      @include media-breakpoint-between(md, xl) {
        margin-left: -60px;
      }
    }
  }
  .text {
    background: #F1F1F1;
    @include fluidAll(padding-top, 50px, 50px, 65px);
    @include fluidAll(padding-bottom, 50px, 75px, 35px);
  }
}
.contentblock--headtext-twocols {
  @include media-breakpoint-up(lgl) {
    .text {
      .container {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-column-gap: 120px;
      }
    }
  }
}