$teaserImageWidth: 263;
$teaserImageHeight: 349;
$ratioTeaser: ($teaserImageHeight*100)/$teaserImageWidth * 1%;

$headerTeaserImageWidth: 305;
$headerTeaserImageHeight: 178.69;
$headerTeaserRatio: ($headerTeaserImageHeight*100)/$headerTeaserImageWidth * 1%;

$teaserEasing: ease-in-out;
$teaserTransitionTime: .3s;

.contentblock .teaser {
  display: block;
  overflow: hidden;
  position: relative;
  transition: transform $teaserEasing $teaserTransitionTime;
  transform: translateY(0);
  @include fluid(padding-bottom, 10px);

  .teaser--image {
    width: 100%;
    height: 0;
    padding-bottom: $ratioTeaser;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .teaser--content {
    background: $color1;
    position: absolute;
    left: 0;
    @include fluid(bottom, 10px);
    width: 100%;
    text-align: center;
    @include fluid(padding-top, 30px);
    @include fluid(padding-right, 20px);
    @include fluid(padding-bottom, 30px);
    @include fluid(padding-left, 20px);
    h5 {
      @include fluidDiff(font-size, 18px, 24px);
      @include fluidDiff(line-height, 21px, 27px);
      transition: color $teaserEasing $teaserTransitionTime;
      color: $white;
      font-weight: 400;
    }
  }
