.gallery--container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.gallery--image {
  display: block;
  img {
    width: 100%;
    height: auto;
  }
}

.gallery--container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
}
.contentblock--gallery-large {
  .gallery--container {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }
}

@include media-breakpoint-up(md) {
  .gallery--container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @include fluid(grid-gap, 30px);
  }
  .cols--2 {
    .gallery--container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}