.contentblock--imageheadtext {

    .container--image {
      img {
        width: 100%;
        height: auto;
      }
    }

  @include media-breakpoint-down(sm) {
    .container {
      &.container--image {
        padding: unset;
        margin: unset;
      }
    }
  }

  .image-headline {

    position: relative;

    .image--background {
      position: absolute;
      height: 50%;
      width: 100%;
      left: 0;
      bottom: 0;
      background-color: #F1F1F1;
      z-index: 1;
    }
    picture {
      position: relative;
      z-index: 2;
    }
    h2 {
      color: $black;
      background: $white;
      display: inline-block;
      @include fluidAll(padding-top, 20px, 20px, 25px);
      @include fluidAll(padding-right, 20px, 22px, 50px);
      @include fluidAll(padding-bottom, 20px, 20px, 25px);
      @include fluidAll(padding-left, 20px, 34px, 50px);
      @include fluid(border-radius, 10px);
      position: absolute;
      z-index: 3;
      @include media-breakpoint-down(sm) {
        left: pxToVwMobile(33px);
        bottom: pxToVwMobile(50px);
        max-width: calc(100% - #{pxToVwMobile(50px)});
      }
      @include media-breakpoint-up(md) {
        margin-left: -34px;
        bottom: 50px;
      }
      @include media-breakpoint-up(xl) {
        bottom: 100px;
        margin-left: unset;
        left: 75px;
      }
      @include media-breakpoint-up(outer) {
        margin-left: -253px;
        left: unset;
      }

      .symbol {
        position: absolute;
        @include fluidAll(left, -25px, -25px, -35px);
        @include fluidAll(top, -25px, -25px, -35px);
      }

    }

  }





}

.frontend {
  .contentblock--imageheadtext {
    .image-headline {
      h2 {
        text-transform: uppercase;
      }
    }
  }
}




/* Symbols */

.symbol {
  background: $color1;
  color: $white;
  @include fluidAll(width, 50px, 50px, 70px);
  @include fluidAll(height, 50px, 50px, 70px);
  @include fluidAll(border-radius, 50px, 50px, 70px);
  left: 0;
  top: 0;
  text-align: center;
  box-shadow: 2px 2px 4px rgba($black, .25);
  i {
    @include fluidAll(line-height, 50px, 50px, 70px);
    &.iconfont-leaf { @include fluidAll(font-size, 29px, 29px, 40px); }
    &.iconfont-wave { @include fluidAll(font-size, 36px, 36px, 50px); }
    &.iconfont-pencil_right { @include fluidAll(font-size, 25px, 25px, 35px); transform: rotate(-270deg); }
    &.iconfont-plus { @include fluidAll(font-size, 29px, 29px, 40px); }
  }
}