

@each $name, $color in $sitecolors {
  .site-#{$name} {
    .contentblock--headtext {
      h2 {
        background-color: $color;
      }
    }
    .contentblock--wysiwyg {
      ul {
        li {
          &:before {
            color: $color;
          }
        }
      }
    }
    symbol {
      background: $color;
    }
  }
}
