.contentblock--infoblock {

}

.infoblock--image {
  @include filled-container(74.782608695652174%);
  margin-bottom: pxToVwMobile(15px);
  @include media-breakpoint-up(lg) {
    padding-bottom: 81.801801801801802%;
    margin-bottom: 0;
  }
  img,
  video {
    @include filled-content;
  }
}


@include media-breakpoint-up(lg) {
  .infoblock {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
}

.infoblock--content {
  border: 1px solid #eee;
  @include fluidDiff(padding, 30px, 60px);
  .contentblock--wysiwyg {
    @include fluid(margin-bottom, 25px);
  }
}

@include media-breakpoint-up(lg) {
  .infoblock {
    &.infoblock--image-right {
      direction: rtl;
      & * {
        direction: ltr;
      }
    }
  }
}