.contentblock--wideimage {
  @include media-breakpoint-up(md) {
    position: relative;
    .wideimage--bottom {
      position: absolute;
      bottom: 0;
      width: 100%;
      .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas: '. wideimage-teaser';
        grid-gap: 30px;
      }
    }
  }
}

.wideimage--image-video {
  @include filled-container(56.231884057971014%);
  width: calc(100% - #{pxToVwMobile(30px)});
  @include fluidDiff(margin-left, 15px, 0);
  @include fluidDiff(margin-right, 15px, 0);
  @include fluidDiff(margin-bottom, 15px, 0);
  @include media-breakpoint-up(md) {
    width: 100%;
    padding-bottom: 41.6666666667%;
  }
  img,
  video {
    @include filled-content;
  }
}

.wideimage--teaser {
  @include fluidDiff(padding-top, 30px, 60px);
  @include fluidDiff(padding-right, 30px, 47px);
  @include fluidDiff(padding-bottom, 30px, 60px);
  @include fluidDiff(padding-left, 30px, 47px);
  grid-area: wideimage-teaser;
  background: $color1;

  h3 {
    color: $white;
    @include fluidDiff(margin-bottom, 12px, 14px);
  }

  p {
    color: $white;
    @include fluidDiff(margin-bottom, 25px, 30px);
  }

}