.contentblock--icongrid {

  .container {
    @include media-breakpoint-down(sm) {
      padding-left: pxToVwMobile(60px);
      padding-right: pxToVwMobile(30px);
    }
  }

}
.iconblocks-grid {
  @include media-breakpoint-up(lgl) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
}
.iconblock {
  display: grid;
  grid-template-columns: pxToVwMobile(55px) auto;
  grid-gap: pxToVwMobile(20px);
  margin-bottom: pxToVwMobile(15px);
  @include media-breakpoint-down(lgl) {
    max-width: 475px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
  }
  @include media-breakpoint-up(md) {
    grid-template-columns: 68px auto;
    grid-gap: 31px;
    border-radius: 3px;
  }
}
.iconblock--icon {
  border: 2px solid #eee;
  @include fluid(border-radius, 3px);
  text-align: center;
  @include fluidDiff(width, 51px, 64px);
  @include fluidDiff(height, 51px, 64px);
  @include fluidDiff(line-height, 51px, 64px);
  display: grid;
  place-items: center;
  i[class^='iconfont'] {
    color: $color2;
    @include fluidDiff(font-size, 33px, 42px);
  }
}
.iconblock--text {
  display: grid;
  place-items: center;
}
.iconblock--text-content {
  width: 100%;
  @include fluidDiff(font-size, 18px, 24px);
  @include fluidDiff(line-height, 21px, 27px);
  color: $color2;
  font-weight: 400;
  font-family: $font-family-headlines;
}