.contentblock--wysiwyg {

  p {
    @include fluidAll(font-size, 16px, 16px, 18px);
    @include fluidAll(line-height, 25px, 25px, 36px);
    @include fluid(margin-bottom, 25px);
    color: $black;
  }

  a {
    color: $color1;
    text-decoration: underline;
  }


  ul {
    list-style-type: none;
    margin: 0;
    @include fluid(margin-bottom, 25px);
    padding: 0;
    li {
      position: relative;
      @include fluid(padding-top, 20px);
      @include fluid(padding-bottom, 20px);
      @include fluid(padding-left, 26px);
      border-bottom: 1px solid rgba($black, 0.1);
      position: relative;
      &:first-child {
        padding-top: 0;
        &:before {
          @include fluid(top, 3px);
        }
      }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none;
      }
      &:before {
        font-family: iconfont!important;
        speak: none;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: "\e900";
        @include fluid(font-size, 18px);
        position: absolute;
        left: 0;
        @include fluid(top, 23px);
      }
    }
  }
  strong {
    font-weight: 700;
    @include fluidAll(font-size, 18px, 18px, 20px);
    @include fluidAll(line-height, 25px, 25px, 30px);
  }

}
.contentblock--wysiwyg-lead {
  p {
    @include fluidDiff(font-size, 18px, 20px);
    @include fluidDiff(line-height, 25px, 30px);
    font-weight: $font-weight-bold;
  }
}


