/* Iconfont */

$icomoon-font-family: "iconfont" !default;
$icomoon-font-path: $font-path !default;

$iconfont-pinterest: "\e911";
$iconfont-arrow_right_small: "\e900";
$iconfont-arrow_right: "\e901";
$iconfont-envelope: "\e902";
$iconfont-magnifier: "\e903";
$iconfont-phone: "\e905";
$iconfont-plus: "\e906";
$iconfont-drop: "\e907";
$iconfont-fax: "\e908";
$iconfont-globe: "\e909";
$iconfont-leaf: "\e90a";
$iconfont-leaf_stylized: "\e90b";
$iconfont-pencil_right: "\e90c";
$iconfont-pencil: "\e90f";
$iconfont-wave: "\e910";
$iconfont-facebook: "\e90d";
$iconfont-poi: "\e90e";
$iconfont-instagram: "\e904";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:
          url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?a9ohi6') format('truetype'),
          url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?a9ohi6') format('woff'),
          url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?a9ohi6##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="iconfont-"], [class*=" iconfont-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


