.contentblock {

  h1, h2, h3, h4, h5 {
    font-family: $font-family-headlines;
    font-weight: 300;
  }

  h1, h2, h3, h4, h5 {
    @include fluidAll(font-size, 25px, 25px, 45px);
    @include fluidAll(line-height, 30px, 30px, 55px);
    color: $colorText;
  }

}

.frontend {
  .contentblock {
    h1, h2, h3, h4, h5 {
      text-transform: uppercase;
    }
  }
}

