/* Selection */

::selection {
  background: $color2;
  color: $white;
}

/* Basic Containers */

html {
  scroll-behavior: smooth;
}
body {
  @include fluidDiff(font-size, $font-size-base-mobile, $font-size-base);
  @include fluidDiff(line-height, $line-height-base-mobile, $line-height-base);
  color: $color2;
}

.page-wrap {
  margin: 0 auto;
  background: $white;
}

main[role=main] {
  @include fluidDiff(padding-top, 50px, 100px);
  @include fluidDiff(margin-bottom, 30px, 100px);
}

/* Container */

.container {
  //max-width: map-get($grid-breakpoints, xl);
  margin: 0 auto;
  @include fluidAll(padding-left, 33px, 109px, 0);
  @include fluidAll(padding-right, 33px, 109px, 0);
  @include media-breakpoint-up(xl) {
    max-width: 1140px;
  }
}




