/* Contentblocks */

.contentblock {

  @include fluidDiff(margin-bottom, 50px, 100px);

  &.margin-bottom {
    &-none {
      @include fluidDiff(margin-bottom, 0, 0);
    }
    &-tiny {
      @include fluid(margin-bottom, 15px);
    }
    &-small {
      @include fluid(margin-bottom, 30px);
    }
  }

  /* Container with grey background */

  &.contentblock--grey {
    background-color: #F1F1F1;
    @include fluidAll(padding-top, 50px, 100px, 100px);
    @include fluidAll(padding-bottom, 50px, 100px, 100px);
    &.margin-bottom {
      &-tiny {
        margin-bottom: 0;
      }
      &-small {
        margin-bottom: 0;
      }
    }
  }

}

/* Columns */

@include media-breakpoint-up(lg) {
  .columns {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
  }
  .columns--3 {
    grid-template-columns: repeat(3, 1fr);
  }
  .columns--4 {
    grid-template-columns: repeat(4, 1fr);
  }
  .columns--5 {
    grid-template-columns: repeat(5, 1fr);
  }
}

