/* basics */
@import 'reboot';
@import 'fonts';
@import 'icons';
@import 'helpers';
@import 'base';

/* elements */
@import 'elements/datepicker';
@import 'elements/form';
@import 'elements/buttons';
@import 'elements/teaser';
@import 'elements/teaser--small';
@import 'elements/teaserslider-controls';
@import 'elements/logos';

/* page-header */
@import 'page-header/page-header';

/* page-footer */
@import 'page-footer/footer';

/* content-elements elements */
@import 'content-elements/teaserslider';
@import 'content-elements/gallery';
@import 'content-elements/table';
@import 'content-elements/divider';
@import 'content-elements/disruptor';
@import 'content-elements/buttons';
@import 'content-elements/teasergrid';
@import 'content-elements/icongrid';
@import 'content-elements/multicolumns';
@import 'content-elements/wideimage';
@import 'content-elements/infoblock';


@import 'content-elements/wysiwyg';
@import 'content-elements/headlines';
@import 'content-elements/quotation';
@import 'content-elements/parallax';
@import 'content-elements/headtext';
@import 'content-elements/imageheadtext';

@import 'content-elements/colors';

@include media-breakpoint-down(sm) {
  .mobile-hidden {
    display: none !important;
  }
}
@include media-breakpoint-down(xl) {
  .desktop-down-hidden {
    display: none !important;
  }
}

// general background for videos

video {
  background-color: $color1dark;
}

// todo: move to own file
.contentblock--image {
  img {
    width: 100%;
    height: auto;
  }
  .image-video {
    width: 100%;
    position: relative;

    video {
      width: 100%;
      height: auto;
    }
  }
}

/* Contact Map */

.contact_map {
  height: 50vh;
  z-index: 1;
}

/* Alert */

.alert {
  @include fluid(padding, 30px);
  @include fluid(border-width, 2px);
  border-style: solid;
  border-color: $color1;
  @include fluid(border-radius, 20px);
  background: rgba($color1, 0.2);
  color: $color1;
  text-align: center;
  @include fluid(margin-bottom, 30px);
  position: relative;
  z-index: 1;
}

/* Preload */

.preload * {
  transition: none !important;
}

.sf-toolbar { display: none !important; }

/* Grids */

.grid {
  @include media-breakpoint-up(md) {
    display: grid;
    & > * {
      height: 100%;
    }
  }
}
.grid--4 {
  @include media-breakpoint-up(md) {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
  @include media-breakpoint-up(lgl) {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
  }
}

/* Prevent blurry display in Chrome */

.page-header--logo,
.main-nav--burger {
  backface-visibility: visible;
  transform: translateZ(0);
}
