@keyframes zoomin {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}
.color-change-2x {
}

@keyframes color-change-2x {
  0% {
    background: #c9bea9;
  }
  100% {
    background: #1290a5;
  }
  100% {
    background: #0d7537;
  }
}

.page-header--image {
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 60vh;
  animation: color-change-2x 13s linear infinite alternate both;
  img, video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: zoomin 25s 1;
    animation-fill-mode: forwards;
  }

}