$logoWidth: 240px;
$logoHeight: 120px;
$logoRatio: (strip-unit($logoHeight)*100)/strip-unit($logoWidth);

.logo,
.footer--logo {
  display: block;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  text-indent: -9999em;
  background-image: svg-load('../_images/svg/biegert.svg');
}

.page-header--logo {
  @include fluidAll(width, 122px, 160px, 240px);
  @include fluidAll(height, 61px, 80px, 120px);
  position: absolute;
  @include fluidAll(top, 14px, 10px, 19px);
  @include fluidAll(left, 25px, 49px, 50px);
  a {
    width: 100%;
    height: 100%;
  }
}

.site-garten { .logo { background-image: svg-load('../_images/svg/biegert-garten.svg'); } }
.site-pool { .logo { background-image: svg-load('../_images/svg/biegert-pool.svg'); } }
.site-schwimmteich { .logo { background-image: svg-load('../_images/svg/biegert.svg'); } }
.site-planung { .logo { background-image: svg-load('../_images/svg/biegert-planung.svg'); } }
.site-zubehoer { .logo { background-image: svg-load('../_images/svg/biegert.svg'); } }

body {
  .footer--logo {
    a {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}